/* The container <div> - needed to position the dropdown content */
.teamheader__right__initiative__dropdown {
  position: relative;
  display: inline-block;
}

/* Dropdown Content (Hidden by Default) */
.teamheader__right__initiative__dropdowncontent {
  display: none;
  position: absolute;
  background-color: #3c3c3c;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 1;
}

/* Links inside the dropdown */
.teamheader__dropdownboxes {
  color: #ffffff;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
  cursor: pointer;
}

/* Change color of dropdown links on hover */
.teamheader__dropdownboxes:hover {
  background-color: #a9a9a9;
}

/* Show the dropdown menu on hover */
.teamheader__right__initiative__dropdown:hover
  .teamheader__right__initiative__dropdowncontent {
  display: block;
}

